class Loader {
  async load(params) {
var name = params.name;
var query = 
{
  "v": 3,
  "q": {
    "find": {
      "out":{
        "$elemMatch": {
          "s1": "19HxigV4QyBv3tHpQVcUEQyq1pzZVdoAut",
          "s3": "text/markdown",
          "s5": {$in: [name,name+'.md']}
          }
      }
    },
    "project": {
      "out.b2":1,
      "out.s2":1,
      "out.s5":1
    },
    "sort": {
      "blk.i": -1, "i": -1
    },
    "limit": 2
  }
};
if (params.txid) {
  query.q.find = {"tx.h":params.txid};
}
//console.log(JSON.stringify(query));
var b64 = btoa(JSON.stringify(query));
var url = "https://neongenesis.bitdb.network/q/1HcBPzWoKDL2FhCMbocQmLuFTYsiD73u1j/" + b64;
//var url = "https://genesis.bitdb.network/q/1FnauZ9aUH2Bex6JzdcV4eNX7oLSSEbxtN/" + b64;

var header = {
  headers: { key: "18doZeakgc7BeKs2L1ZWKMnoJnRQmoJy2i" }
};
var json = await (await fetch(url, header)).json();
var tx =(json.u[0]||json.c[0]);
if (!tx) return tx;
var content = tx.out[0].s2;
return {content,tx};
}
}
export default new Loader();

